"use client";

import { Component } from "@/types/component";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import { Upload, File, X, FileText, Image as ImageIcon } from "lucide-react";
import { useState, useRef } from "react";
import { cn } from "@/lib/utils";

interface FileUploadComponentProps {
  component: Component;
  value: any;
  onChange: (value: any) => void;
  index: number;
}

export default function FileUploadComponent({
  component,
  value,
  onChange,
  index,
}: FileUploadComponentProps) {
  const settings = component.settings as any;
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [isDragging, setIsDragging] = useState(false);

  const isPdfOnly = component.type === "pdf_upload";
  const isImageOnly = component.type === "image_upload";
  const isVideoOnly = component.type === "video_upload";
  const allowMultiple = settings.allowMultiple || false;
  const maxFiles = settings.maxFiles || 1;
  const maxFileSize = settings.maxFileSize || 10; // MB

  const files = Array.isArray(value) ? value : value ? [value] : [];

  const handleFileSelect = async (selectedFiles: FileList | null) => {
    if (!selectedFiles || selectedFiles.length === 0) return;

    const newFiles = Array.from(selectedFiles);

    // Validate file types
    if (isPdfOnly) {
      const invalidFiles = newFiles.filter((file) => file.type !== "application/pdf");
      if (invalidFiles.length > 0) {
        alert("ÙŠØ±Ø¬Ù‰ Ø±ÙØ¹ Ù…Ù„ÙØ§Øª PDF ÙÙ‚Ø·");
        return;
      }
    } else if (isImageOnly) {
      const invalidFiles = newFiles.filter((file) => !file.type.startsWith("image/"));
      if (invalidFiles.length > 0) {
        alert("ÙŠØ±Ø¬Ù‰ Ø±ÙØ¹ ØµÙˆØ± ÙÙ‚Ø·");
        return;
      }
    } else if (isVideoOnly) {
      const invalidFiles = newFiles.filter((file) => !file.type.startsWith("video/"));
      if (invalidFiles.length > 0) {
        alert("ÙŠØ±Ø¬Ù‰ Ø±ÙØ¹ ÙÙŠØ¯ÙŠÙˆÙ‡Ø§Øª ÙÙ‚Ø·");
        return;
      }
    }

    // Validate file sizes
    const oversizedFiles = newFiles.filter(
      (file) => file.size > maxFileSize * 1024 * 1024
    );
    if (oversizedFiles.length > 0) {
      alert(`Ø­Ø¬Ù… Ø§Ù„Ù…Ù„Ù ÙŠØ¬Ø¨ Ø£Ù† Ù„Ø§ ÙŠØªØ¬Ø§ÙˆØ² ${maxFileSize} Ù…ÙŠØ¬Ø§Ø¨Ø§ÙŠØª`);
      return;
    }

    // Convert files to base64 with metadata
    const convertedFiles = await Promise.all(
      newFiles.map(async (file) => {
        const base64 = await fileToBase64(file);
        return {
          fileName: file.name,
          fileSize: file.size,
          fileType: file.type,
          fileData: base64,
          uploadedAt: new Date().toISOString(),
        };
      })
    );

    // Validate number of files
    if (allowMultiple) {
      const totalFiles = files.length + convertedFiles.length;
      if (totalFiles > maxFiles) {
        alert(`ÙŠÙ…ÙƒÙ†Ùƒ Ø±ÙØ¹ ${maxFiles} Ù…Ù„ÙØ§Øª ÙƒØ­Ø¯ Ø£Ù‚ØµÙ‰`);
        return;
      }
      onChange([...files, ...convertedFiles]);
    } else {
      onChange(convertedFiles[0]);
    }
  };

  // Helper function to convert file to base64
  const fileToBase64 = (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = () => resolve(reader.result as string);
      reader.onerror = reject;
      reader.readAsDataURL(file);
    });
  };

  const handleRemoveFile = (index: number) => {
    if (allowMultiple) {
      const newFiles = files.filter((_: any, i: number) => i !== index);
      onChange(newFiles.length > 0 ? newFiles : null);
    } else {
      onChange(null);
    }
  };

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(true);
  };

  const handleDragLeave = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
    handleFileSelect(e.dataTransfer.files);
  };

  const formatFileSize = (bytes: number) => {
    if (bytes < 1024) return bytes + " B";
    if (bytes < 1024 * 1024) return (bytes / 1024).toFixed(1) + " KB";
    return (bytes / (1024 * 1024)).toFixed(1) + " MB";
  };

  const getFileIcon = (file: any) => {
    const fileType = file.fileType || file.type || "";
    if (fileType.startsWith("image/")) return ImageIcon;
    if (fileType === "application/pdf") return FileText;
    return File;
  };

  const getFileName = (file: any) => {
    return file.fileName || file.name || "Ù…Ù„Ù";
  };

  const getFileSize = (file: any) => {
    return file.fileSize || file.size || 0;
  };

  return (
    <div className="space-y-4">
      {/* Label */}
      <div className="flex items-start gap-3">
        <span className="flex-shrink-0 w-8 h-8 bg-primary/10 text-primary rounded-full flex items-center justify-center font-bold text-sm">
          {index + 1}
        </span>
        <div className="flex-1">
          <Label className="text-xl font-semibold text-gray-900 leading-relaxed">
            {settings.label || "Ø±ÙØ¹ Ù…Ù„Ù"}
            {settings.required && <span className="text-red-500 mr-1">*</span>}
          </Label>
          {settings.description && (
            <p className="text-gray-600 mt-2 text-base leading-relaxed">
              {settings.description}
            </p>
          )}
        </div>
      </div>

      {/* Upload Area */}
      <div className="pr-11 space-y-4">
        {/* Drag & Drop Area */}
        <div
          onDragOver={handleDragOver}
          onDragLeave={handleDragLeave}
          onDrop={handleDrop}
          className={cn(
            "border-2 border-dashed rounded-xl p-8 text-center transition-all cursor-pointer",
            isDragging
              ? "border-primary bg-blue-50"
              : "border-gray-300 hover:border-primary/50 hover:bg-gray-50"
          )}
          onClick={() => fileInputRef.current?.click()}
        >
          <div className="flex flex-col items-center gap-4">
            <div className="w-16 h-16 bg-blue-50 rounded-full flex items-center justify-center">
              <Upload className="w-8 h-8 text-primary" />
            </div>
            <div>
              <p className="text-lg font-semibold text-gray-900 mb-1">
                Ø§Ø³Ø­Ø¨ Ø§Ù„Ù…Ù„ÙØ§Øª Ù‡Ù†Ø§ Ø£Ùˆ Ø§Ù†Ù‚Ø± Ù„Ù„Ø§Ø®ØªÙŠØ§Ø±
              </p>
              <p className="text-sm text-gray-600">
                {isPdfOnly ? "Ù…Ù„ÙØ§Øª PDF ÙÙ‚Ø·" : isImageOnly ? "ØµÙˆØ± ÙÙ‚Ø·" : isVideoOnly ? "ÙÙŠØ¯ÙŠÙˆÙ‡Ø§Øª ÙÙ‚Ø·" : "Ø¬Ù…ÙŠØ¹ Ø£Ù†ÙˆØ§Ø¹ Ø§Ù„Ù…Ù„ÙØ§Øª"}
                {" â€¢ "}
                Ø­Ø¬Ù… Ø£Ù‚ØµÙ‰ {maxFileSize} Ù…ÙŠØ¬Ø§Ø¨Ø§ÙŠØª
                {allowMultiple && ` â€¢ Ø­ØªÙ‰ ${maxFiles} Ù…Ù„ÙØ§Øª`}
              </p>
            </div>
          </div>
        </div>

        {/* Hidden File Input */}
        <input
          ref={fileInputRef}
          type="file"
          accept={isPdfOnly ? ".pdf" : isImageOnly ? "image/*" : isVideoOnly ? "video/*" : "*"}
          multiple={allowMultiple}
          onChange={(e) => handleFileSelect(e.target.files)}
          className="hidden"
        />

        {/* Uploaded Files List */}
        {files.length > 0 && (
          <div className="space-y-2">
            {files.map((file: any, idx: number) => {
              const FileIcon = getFileIcon(file);
              return (
                <div
                  key={idx}
                  className="flex items-center gap-3 p-4 bg-gray-50 rounded-lg border border-gray-200"
                >
                  <div className="w-10 h-10 bg-white rounded-lg flex items-center justify-center border border-gray-200">
                    <FileIcon className="w-5 h-5 text-gray-600" />
                  </div>
                  <div className="flex-1 min-w-0">
                    <p className="font-medium text-gray-900 truncate">
                      {getFileName(file)}
                    </p>
                    <p className="text-sm text-gray-500">
                      {formatFileSize(getFileSize(file))}
                    </p>
                  </div>
                  <Button
                    type="button"
                    variant="ghost"
                    size="sm"
                    onClick={() => handleRemoveFile(idx)}
                    className="text-red-600 hover:text-red-700 hover:bg-red-50"
                  >
                    <X className="w-5 h-5" />
                  </Button>
                </div>
              );
            })}
          </div>
        )}

        {/* Upload Another Button (for multiple files) */}
        {allowMultiple && files.length > 0 && files.length < maxFiles && (
          <Button
            type="button"
            variant="outline"
            onClick={() => fileInputRef.current?.click()}
            className="w-full"
          >
            <Upload className="w-4 h-4 mr-2" />
            Ø±ÙØ¹ Ù…Ù„Ù Ø¢Ø®Ø± ({files.length}/{maxFiles})
          </Button>
        )}
      </div>
    </div>
  );
}

