/**
 * ØªØ­ÙˆÙŠÙ„ Ù†ÙˆØ¹ Ø§Ù„Ø³Ø¤Ø§Ù„ Ù…Ù† Ù†Øµ Ø¥Ù„Ù‰ Ø±Ù‚Ù…
 * Frontend QuestionType â†’ Backend ComponentType Enum
 */
const mapQuestionTypeToNumber = (type: string): number => {
  const types: Record<string, number> = {
    "single_choice": 1,
    "multiple_choice": 2,
    "short_text": 3,
    "long_text": 4,
    "dropdown": 5,
    "yes_no": 6,
    "linear_scale": 7,
    "choice_grid": 8,
    "number": 9,
    "email": 10,
    "phone": 11,
    "date": 12,
    "time": 13,
    "rating": 14,
    "file_upload": 15,
    "signature": 16,
    "text": 17,
    "display": 18,
  };
  return types[type] || 3; // Default to short_text
};

/**
 * ØªØ­ÙˆÙŠÙ„ Ø§Ù„Ù…ÙƒÙˆÙ† Ù…Ù† Frontend Ø¥Ù„Ù‰ Backend
 */
export const mapComponentToBackend = (component: import("@/types/component").Component, order: number) => {
  const settings = component.settings as any;

  // ØªØ­ÙˆÙŠÙ„ Ø§Ù„Ø®ÙŠØ§Ø±Ø§Øª Ø¥Ù„Ù‰ JSON
  let optionsJson: string | null = null;
  if (settings.choices && settings.choices.length > 0) {
    optionsJson = JSON.stringify(settings.choices.map((c: any) => ({
      id: c.id,
      label: c.label,
      value: c.value,
    })));
  }

  // ØªØ­ÙˆÙŠÙ„ Ø§Ù„Ø¥Ø¬Ø§Ø¨Ø© Ø§Ù„ØµØ­ÙŠØ­Ø© Ø¥Ù„Ù‰ JSON
  let correctAnswerJson: string | null = null;
  if (settings.correctAnswer !== undefined) {
    correctAnswerJson = JSON.stringify(settings.correctAnswer);
  }

  return {
    type: mapQuestionTypeToNumber(settings.questionType || component.type),
    order: order,
    title: settings.label || null,
    description: settings.description || null,
    placeholder: settings.placeholder || null,
    isRequired: settings.required || false,
    isVisible: true,
    optionsJson: optionsJson,
    validationJson: null,
    correctAnswerJson: correctAnswerJson,
    points: settings.points || null,
    explanation: null,
    minValue: settings.scaleMin || null,
    maxValue: settings.scaleMax || null,
    minLabel: settings.scaleMinLabel || null,
    maxLabel: settings.scaleMaxLabel || null,
    mediaUrl: null,
    mediaType: null,
    styleJson: null,
  };
};

