"use client";

import { Component } from "@/types/component";
import { Label } from "@/components/ui/label";
import { Input } from "@/components/ui/input";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { useEffect, useState } from "react";

interface TableComponentProps {
  component: Component;
  value: any;
  onChange: (value: any) => void;
  index: number;
}

export default function TableComponent({
  component,
  value,
  onChange,
  index,
}: TableComponentProps) {
  const settings = component.settings as any;
  const columns = settings.columns || [];
  const rows = settings.rows || [];
  const enableCalculations = settings.enableCalculations || false;

  // Initialize table data
  const [tableData, setTableData] = useState<Record<string, Record<string, string>>>(() => {
    if (value) return value;

    const initialData: Record<string, Record<string, string>> = {};
    rows.forEach((row: any) => {
      initialData[row.id] = {};
      columns.forEach((col: any) => {
        initialData[row.id][col.id] = "";
      });
    });
    return initialData;
  });

  // Update parent when table data changes
  useEffect(() => {
    onChange(tableData);
  }, [tableData, onChange]);

  const handleCellChange = (rowId: string, colId: string, value: string) => {
    setTableData((prev) => ({
      ...prev,
      [rowId]: {
        ...prev[rowId],
        [colId]: value,
      },
    }));
  };

  // Calculate column sum
  const calculateColumnSum = (colId: string) => {
    let sum = 0;
    rows.forEach((row: any) => {
      const value = parseFloat(tableData[row.id]?.[colId] || "0");
      if (!isNaN(value)) {
        sum += value;
      }
    });
    return sum;
  };

  // Calculate row sum
  const calculateRowSum = (rowId: string) => {
    let sum = 0;
    columns.forEach((col: any) => {
      const value = parseFloat(tableData[rowId]?.[col.id] || "0");
      if (!isNaN(value)) {
        sum += value;
      }
    });
    return sum;
  };

  return (
    <div className="space-y-4">
      {/* Label */}
      <div className="flex items-start gap-3">
        <span className="flex-shrink-0 w-8 h-8 bg-primary/10 text-primary rounded-full flex items-center justify-center font-bold text-sm">
          {index + 1}
        </span>
        <div className="flex-1">
          <Label className="text-xl font-semibold text-gray-900 leading-relaxed">
            {settings.label || "Ø¬Ø¯ÙˆÙ„"}
            {settings.required && <span className="text-red-500 mr-1">*</span>}
          </Label>
          {settings.description && (
            <p className="text-gray-600 mt-2 text-base leading-relaxed">
              {settings.description}
            </p>
          )}
        </div>
      </div>

      {/* Table */}
      <div className="pr-11 overflow-x-auto">
        <div className="border border-gray-200 rounded-lg overflow-hidden">
          <Table>
            <TableHeader>
              <TableRow className="bg-gray-50">
                <TableHead className="font-bold text-gray-900 text-right">
                  {/* Empty cell for row headers */}
                </TableHead>
                {columns.map((col: any) => (
                  <TableHead key={col.id} className="font-bold text-gray-900 text-center">
                    {col.label}
                  </TableHead>
                ))}
                {enableCalculations && (
                  <TableHead className="font-bold text-gray-900 text-center bg-blue-50">
                    Ø§Ù„Ù…Ø¬Ù…ÙˆØ¹
                  </TableHead>
                )}
              </TableRow>
            </TableHeader>
            <TableBody>
              {rows.map((row: any) => (
                <TableRow key={row.id}>
                  <TableCell className="font-semibold text-gray-900 bg-gray-50">
                    {row.label}
                  </TableCell>
                  {columns.map((col: any) => (
                    <TableCell key={col.id} className="p-2">
                      <Input
                        type={col.type === "number" ? "number" : "text"}
                        value={tableData[row.id]?.[col.id] || ""}
                        onChange={(e) => handleCellChange(row.id, col.id, e.target.value)}
                        className="text-center"
                        placeholder="..."
                      />
                    </TableCell>
                  ))}
                  {enableCalculations && (
                    <TableCell className="font-bold text-center bg-blue-50">
                      {calculateRowSum(row.id).toFixed(2)}
                    </TableCell>
                  )}
                </TableRow>
              ))}
              {enableCalculations && (
                <TableRow className="bg-blue-50 font-bold">
                  <TableCell className="text-gray-900">Ø§Ù„Ù…Ø¬Ù…ÙˆØ¹</TableCell>
                  {columns.map((col: any) => (
                    <TableCell key={col.id} className="text-center">
                      {calculateColumnSum(col.id).toFixed(2)}
                    </TableCell>
                  ))}
                  <TableCell className="text-center">
                    {rows
                      .reduce((sum: number, row: any) => sum + calculateRowSum(row.id), 0)
                      .toFixed(2)}
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </div>
      </div>
    </div>
  );
}

